.dashboard-topbar{
    height: 50px;
    background-color: #ebdc08;
    z-index: 1;
}
#menuToggle
{
  display: block;
  position: relative;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

#menuToggle a
{
  text-decoration: none;
  color: #111;
  transition: color 0.3s ease;
}
#menuToggle a:hover
{
  color: #888;
}

#menuToggle input
{
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  cursor: pointer;
  opacity: 0; /* hide this */
  z-index: 2; /* and place it over the hamburger */
  -webkit-touch-callout: none;
}
.dashboard-bar-icon{
    position: absolute;
    left: 20px;
    font-size: 25px;
    color: #555;
    z-index: 3;
    cursor: pointer;
    top: -45px;
}
.backtoSite-btn{
  border: none;
 position: relative;
 top: 10px;
 padding: 4px 20px;
 background-color: #01050a;
 color: #fff;
 border-radius: 20px;
 font-weight: 500;
}
.backtoSite-btn:hover{
 background-color: #ffffff;
 color: #000000;
 border-radius: 20px;
 font-weight: 500;
}
.dashboard-logo{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}
.dashboard-logo img{
  width:100px;
  height: 100px;
  position: relative;
  left: -40px;
  /* padding-top: 20px; */
}
#menuToggle #select-icon:checked ~ .dashboard-bar-icon
{
  opacity: 0;
  background:#111;
}
@media only screen and (min-width:992px) {
  .dashboard-bar-icon,
  .dashboard-bar-cross-icon{
    opacity: 0;
    visibility: hidden;
  }
  .manu-check-btn{
    visibility: hidden;
  }
}

#menu
{
  position:absolute;
  width: 260px;
  top: -50px;
  text-align: left;
  background: #e9f90c;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  /* to stop flickering of text in safari */
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  height:auto;
  padding-bottom:50px;
}

#menu li
{
  padding: 5px 0px;
  font-size: 20px;
  padding-left: 5px;
}
#menu li:hover{
  background-color: #111;
  color: #fff;
  border-radius: 61px 0px 0px 61px;
  padding-left: 30px;
  padding: 5px 20px;
  margin-left: -10px;
}
#menu li:hover i{
 color: #1989E8;
}

.navImgStyle{
  margin-top: -80px;
}
.navImgStyle h2{
  font-size: 20px;
  color: white;
  margin-left: 35%;
  margin-top: 5px;
}

.nav-icon-style{
  padding-right: 18px;
}


/*
 * And let's slide it in from the left
 */
#menuToggle input:checked ~ ul
{
  transform: none;
}

.last-text p{
  margin-top: 50px;
  margin-left: -20px;
  font-weight: 500;
font-size: 15px;
text-align: center;
letter-spacing: 0.216em;
}
.navBottomIcon{
  display: flex;
  justify-content: center;
  font-size: 30px;
  color: #002A5B !important;
  margin-left: -20px;
}
.navBottomIcon i{
  background-image:linear-gradient(to bottom, #40CAEE, #1B32FD);
  height: 40px;
  width:40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  margin: 0 10px;
}