.product-add-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.add-in-field{
    outline: none;
    padding: 5px 5px;
    border: 2px solid #aaa;
    border-radius: 10px;
}
.multi-form{
 text-align: left;
 display: flex;
 flex-wrap: wrap;
 justify-content: space-between;
 margin-top: 20px;
}
.add-btn{
    outline: none;
    border: 2px solid #090;
    width: 35px;
    border-radius: 50%;
    padding-bottom: 2px;
    font-size: 20px;
    font-weight: 700;
}
.size-field{
    width: 40px;
    text-align: center;
    border: 1px solid #aaa;
    border-radius: 10px;
}
.multi-form{
    text-align: left;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
   }
.size-clear-btn-container{
    display: flex;
    align-items:flex-start;
    width: 90%;
    justify-content: flex-start;
}
.size-clear-btn{
    border: none;
    background-color: #f05;
    color: #fff;
    font-size: 20px;
    padding: 2px 5px;
    border-radius: 5px;
    position: relative;
    }
.submite-btn{
    margin: 20px 0;
    width: 200px;
    padding: 5px;
    border-radius: 10px;
    outline: none;
    background-color: #090;
    color: #fff;
    border: none;
    font-size: 20px;
}
@media only screen and (min-width:800px){
    .add-in-field,.size-clear-btn-container{
        width: 400px;
        margin-top: 10px;
    }
    .multi-form{
        width: 400px;
        margin-top: 25px;
    }
}
@media only screen and (max-width:799px){
    .add-in-field{
        width: 90%;
        margin-top: 20px;
    }
    .multi-form{
        text-align: left;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 20px;
        width: 90%;
    }
}
