.ordered-img{
    height: 80px;
    width: 80px;
    border-radius: 5px;
    margin: 2px;
}
.order-product{
display: flex;
align-items: center;
}
.product-de{
    text-align: left;
    padding-left: 10px;
}
.ready-for-shipping{
    border: none;
    padding: 5px 10px;
    background-color: rgb(16, 198, 95);
    color: aliceblue;
    font-weight: 500;
    border-radius: 5px;
}
.shipping-done{
    background-color: rgb(210, 239, 44);
    padding: 5px 10px;
}