*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*--------------------add to cart button
--------------------------------------------// */
.addtoCart{
  width:150px;
  height: 30px;
  background-color: #FE4ECD;
  color: #eee;
  font-size: 18px;
  font-weight: 500;
  border-radius: 10px;
  border: none;
  outline: none;
  padding-bottom: 5px;
}
/* Buy Now button/ */
.buynow-btn{
  width: 120px;
  height: 30px;
  color: #222;
  background-color: #83DAFF;
  border-radius: 10px;
  border:none;
  font-size: 18px;
  font-weight: 500;
  
}

/* -------------------see more button
-------------------------------------------// */
.see-more-btn{
  width: 150px;
  height: 40px;
  background-color: #FE4ECD;
  color: #eee;
  font-size: 20px;
  font-weight: 600;
  border-radius: 20px;
  border: none;
  outline: none;
  padding-bottom: 5px;
}
.see-more-btn a{
 text-decoration: none;
 color: #fff;
}

/*-------------------- heading
-------------------------------------------//*/
.primary-heading{
  font-size: 50px;
  color: #eee;
  font-weight: 700;
}
.secondary-heading{
  font-size: 40px;
  color: #eee;
  font-weight: 600;
}
/* .small-heading{
  
} */
@media only screen and (max-width:992px) {
  .primary-heading{
    font-size: 35px;
    color: rgb(245, 246, 247);
    font-weight: 600;
    /* line-height: 1.2; */
  }
  /* .secondary-heading{
  
  }
  .small-heading{
    
  } */
}
@media only screen and (max-width:768px) {
  .primary-heading{
    font-size: 22px;
    font-weight: 600;
    line-height: 1;
  }
  /* .secondary-heading{
  
  }
  .small-heading{
    
  } */
  .see-more-btn{
    width: 100px;
    height: 27px;
    font-size: 16px;
    font-weight: 600;
  }
  .buynow-btn{
    width:auto;
    color: #222;
    background-color: #83DAFF;
    border-radius: 5px;
    border:none;
    font-size: 14px;
    font-weight: 500;
    padding: 0 10px;
  }
  .addtoCart{
    width:auto;
    height: 30px;
    background-color: #FE4ECD;
    color: #eee;
    font-size: 14px;
    font-weight: 500;
    border-radius: 10px;
    border: none;
    outline: none;
    padding-bottom: 5px;
  }
}
