.quantity{
    display: flex;
}
.input-line-text{
    font-size: 20px;
    font-weight: 500;
}
.minifield{
    width: 50px;
    text-align: center;
    padding-left: 5px;
}
.img-product{
    max-width: 90px;
}
.size{
    margin-top: -15px;
}
#quantityValue{
    width: 50px;
    text-align: center;
    border: none;
    font-weight: 700;
}
#totalPrice{
    width: 80px;
    text-align: center;
    border: 1px solid green;
    background-color: #83DAFF;
    border-radius: 5px;
    padding-left: 8px;
    height: 25px;
}
.quantity-div{
    margin-bottom: -5px;
}
.quantity-btn{
    width: 50px;
    border: none;
    background-color: #83DAFF;
    color: #111;
    font-weight: 800;
    border-radius: 5px;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    margin: 0 5px;
    
}
.product-card{
    display: flex;
    align-items: center;
    /* justify-content: space-around; */
}
.submit-btn-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.submit-btn{
  width: 50%;
  background-color: #111;
  color: #fff;
  padding: 3px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
}
.shipping-address-field{
    width: 80%;
}