.btn-primary{
    background-color: transparent;
    color: #000;
    border: none;
}
.btn-primary:hover{
    background-color: transparent;
    color: #000;
}
.btn-primary:focus{
    background-color: transparent;
    color: #000;
}
.drop-btn{
    display: block;
    width: 100%;
    padding-left: 10px;
}
.drop-btn:hover{
    background-color:#83DAFF;
}
.drop-btn:focus{
    background-color:rgb(37, 137, 251);
}
/*========== navbar section 
====================================*/
.nav{
    position: relative;
    height: 50px;
    background-color: #eee;
    width: 100%;
    margin-top: -2px;
}
 .nav ul{
    position: absolute;
    left: 0px;
    top:0;
    line-height: 50px;
}
#manu-items{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.nav ul li{
    display: inline-block;
    margin-right: 30px;
}
 .nav ul li a{
    text-decoration: none;
    color:black;
    font-weight: 600;
}
#sort-menu{
    position: absolute;
    color:white;
    display: none;
}
#menu-yes{
    position: absolute;
    top:0;
    right:20px;
    color: black;
    font-size: 30px;
    line-height:50px;
    cursor: pointer;
    display:none;
}
.menu-no{
    position: absolute;
    right:10px;
    font-size: 26px;
    display: none;
    color: #000;
    top: -15px;
}
#menu-no{
    top:0;
    position: absolute;
    right:20px;
    color: #000;
    font-size: 30px;
    line-height:50px;
    cursor: pointer;
    display:none;
}

/* manu responsive */
@media only screen and (max-width:992px){
    #manu-items{
        display:block;
    }
#menu-yes{
    display:block;
}
.menu-no{
    display: inline-block;
}
 .nav ul{
    display: block;
    position: fixed;
    left:-100%;
    height:auto;
    top:40px;
    width:20%;
    z-index:20; 
    text-align: center;  
    background-color: #fff ;
    transition: .5s;
    padding-bottom: 20px;
 }
 .nav ul li{
     display:flex;
     flex-direction: column;
     margin-right:10%;
     border-bottom: 2px solid ;
     border-image:linear-gradient(to right, #000, #ddd)30;
     line-height: 3;
}
 #sort-menu:checked ~ ul{
    left: 0;
    width: 60%;
    display: block;
 }
 #sort-menu:checked ~#sort-menu #manu-yes{
   color: none;
 }

 }