table, th, td {
    border:1px solid black;
  }
  .shipping{
    border: none;
    background-color: rgb(28, 8, 255);
    color: #fff;
    font-weight: 500;
    padding: 2px;
    margin: 0 5px;
  }
  .new-order{
    border: none;
    background-color: rgb(255, 8, 107);
    color: #fff;
    font-weight: 500;
    padding: 2px;
    margin: 0 5px;
  }
  .orderTable td{
    padding: 5px;
    margin: 0 5px;
  }
  .return{
    padding: 2px;
    margin: 0 5px;
    border: none;
  }
  .success{
    padding: 2px;
    margin: 0 5px;
    border: none;
  }
  .details{
    border: none;
    padding: 2px 5px;
    background-color: rgb(7, 151, 60);
  }
  .details a{
    text-decoration: none;
    color: #fff;
  }
  .details:hover{
    background-color: rgb(226, 83, 191);
  }