.manage-product-img{
    max-width: 150px;
    margin: 10px;
}
.manage-product-info{
    text-align: start;
    font-size: 20px;
    font-weight: 600;
    text-align: left;
    margin-top: 20px;
}
.manage-btn button{
    font-size: 20px;
    font-weight: 600;
    color: #eee;
    margin: 10px;
    padding: 3px 10px;
    border: 1px solid #555;
    border-radius: 5px;
}
.manage-btn button a{
    color: #eee;
    text-decoration: none;
}
