.item-card{
    position: relative;
}
.remove-from-cart-btn{
    position: absolute;
    width: 30px;
    height: 30px;
    right: 5px;
    top: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.update-btn{
    width: 40px;
    margin-right: 10px;
    background-color: #f50;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    height: 30px;
    line-height: 0;
    border: none;
    border-radius: 5px;
}
.empty-basket{
    width: 10%;
}
.cuponfield{
    border-radius: 10px;
    border: none;
    background-color: #f50;
    outline: none;
    padding: 5px;
    font-size: 20px;
    font-weight: 700;
    width: 150px;
    text-align: center;
}
.user-input{
    padding: 2px 5px;
    border: 1px solid rgb(118, 94, 82);
    border-radius: 10px;
    outline: none;
}