.banner-section{
    max-width: 100vw;
    position: relative;
}
.banner-img{
    width: 100%;
    z-index: -1;
}
.banner-heading{
 position: absolute;
 top: 0;
 width: 40%;
 text-align: left;
 left: 10%;
 top: 25%;
}
.responsive-button{
    width: 150px;
    height: 40px;
    font-size: 20px;
    position: absolute;
    left: 10%;
    bottom: 10%;
    border: none;
    border-radius: 20px;
    background-color: #275e80;
    color: #eee;
    font-weight: 600;
}
.responsive-button a{
 text-decoration: none;
 color: #eee;
}
@media only screen and (max-width:768px) {
    .responsive-button{
        position: absolute;
        left: 70%;
        width: 100px;
        height: 25px;
        font-size: 16px;
        right: 10%;
        bottom: 5%;       
    }
}