.search-bar{
    background-color: #eee;
    height: auto;
    padding-top: 10px;
}
.search-cart{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.logo a{
    text-decoration: none;
    color: #333;
}
.inner-logo{
    height: 100px;
    width: 100px;
    position: relative;
    right:5px;
}
.logo-name{
    font-size: 25px;
    font-weight: 600;
}
.search-cart{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    margin-top: 33px;
}
.home-searchbar{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.search-input-home{
    width: 100%;
    height: 40px;
    padding: 10px;
    border-radius: 20px;
    outline: none;
    border: 1px solid #888;
    position: relative;
}
