.search-icon{
    position: relative;
    left: -40px;
    font-size: 20px;
    color: #888;
}
.cart-section{
    display: flex;
}
.cart-section i{
    font-size: 35px;
    color: #111;
}
.cart-counter{
    background-color: tomato;
    color: #fff;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    position: relative;
    left: -10px;
    top: -10px;
}
@media only screen and (max-width:792px){
    .inputCartWraper{
        height:50px;
    }
    .search-bar{
        height:auto;
    } 
    .search-cart{
        position: relative;
        top:-30px;
    }
    .cart-section{
        position: relative;
        left: 20px;
    }
    .home-searchbar{
        position: relative;
        left: 8%;
    }
}
