.sort-category{
    background-color:#eee;
    margin-top: 15px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding-top: 20px;
    padding-bottom: 20px;
    position: absolute;
    width: 190px;
    
}
.product-size{
    background-color: #eee;
    font-weight: 400;
    padding: 5px;
    width: 100%;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
.size-box{
    display: flex;
    flex-wrap: wrap !important;
    align-items: center;
    justify-content: flex-start;
}
.card-price{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.bdt-show{
    font-size: 17px;
    padding-right:5px;
    font-weight: 600;
}
.size-box span{
    font-size: 14px !important;
    background-color: #333;
    margin: 3px;
    padding: 2px 5px;
    color: #fff;
    border-radius: 5px;
}
.select-form{
    display: flex;
    padding-left: 10px;
}
.category-wraper{
    margin-left: 10px;
    width: 100%;
}
.order-link{
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    color: #000;
}
.search-category{
 font-size: 16px;
 font-weight: 600;
 border: none;
 /* background-color:#ccc; */
 padding: 0 5px;
 width: 100%;
 text-align: left;
 margin: 2px 0;
 padding-bottom: 2px;
 border-top-left-radius: 15px;
 border-bottom-left-radius: 15px;
}
.search-category:focus{
background-color: #fff;
}
.search-category:hover{
background-color:#fff;
cursor: pointer;
padding-bottom: 2px;
border-top-left-radius: 15px;
border-bottom-left-radius: 15px;
}
.shop-loader{
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media only screen and (max-width:500px ){
    .select-form{
        display: flex;
        padding-left: 2px;
        
    }
}
@media only screen and (min-width:700px ){
    .shop-wraper{
        min-height: 600px;
    }
}
@media only screen and (max-width:950px ){
    .sort-category{
        display: none;
    }
}