.display-section{
    position: relative;
    margin-top: 50px;
    background-color: rgb(226, 226, 226);
    color: #000000;
    padding: 10px 0;
}
.display-section a{
    text-decoration: none;
    color: #000000;
}
.product-card{
    border: none;
    position: relative;
    margin-top: 10px;
}
.home-slide-product-name{
    background-color: rgb(8, 19, 79);
    color: aliceblue;
}

.display-banner{
  width: 100%;
  min-height: 110px;
}
.display-banner-img{
  width: 50%;
  height: 100%;
  /* background-color: red; */
  overflow:hidden ;
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.product-banner{
  height: 100%;
  position:absolute;
  display: flex;
  align-items: center;
  justify-content: space-around;
  top: 0;
  width: 100%;
}
.see-more{
  background-color: #dddd;
  border: none;
  padding: 7px 40px;
  font-size: 17px;
  font-weight: 500;
  border-radius: 5px;
}
.see-more:hover{
  background-color: #000;
  border: none;
  color: #fff;
}
/* ---------------------swiper slider// */
.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .swiper-horizontal{
    margin-top: 20px !important;
  }
  .swiper-pagination{
    position: relative !important;
    margin-top: 5px !important;
  } 


/* //--------------end swiper ---------------- */
